<template>
	<q-dialog
		:value="showImportDevices"
		persistent
		>
		
		<q-card class="column full-height" style="width: 1000px; max-width: 80vw;">
			<q-card-section class="row items-center">
				<div class="text-h6">{{ $t('Import Devices') }}</div>
				
				<q-space />
				<q-btn flat round color="grey" icon="close" @click="onClose" />
			</q-card-section>
			<q-separator />
			
			<q-card-section class="col q-pa-sm scroll body">
				<div class="text-h6 text-header primary">{{ $t('Adapter') }}</div>
				<q-separator style="margin: 0 -16px" />
				
				<q-field :label="$t('Search the adapter structure for all devices or only devices added to enums')" stack-label borderless class="q-pa-sm">
					<template v-slot:control>
						<q-option-group
							v-model="adapterStructure"
							:options="adapterStructureOptions"
							color="primary"
							inline
							/>
						
					</template>
				</q-field>
				
				<!-- ADAPTER SELECTION -->
				<q-expansion-item
					v-model="expanded['_adapterList']"
					class="paper shadow-1 rounded-borders full-width"
					>
					
					<template v-slot:header>
						<q-item-section>
							{{ $t('Adapter List') }}
						</q-item-section>

						<q-item-section side v-if="state === 'loaded'">
							<q-chip color="secondary" text-color="white">
								{{ $t('Devices') }}: {{ importedDevicesLength.all }}
							</q-chip>
						</q-item-section>
					</template>
					
					<q-list dense style="margin-bottom: 16px">
						<q-item tag="label" v-ripple @click="toggleAllAdapterListItems">
							<q-item-section side>
								<q-checkbox :value="selectedAdapters.length !== 0" />
							</q-item-section>

							<q-item-section>
								<q-item-label><em>{{ $t('(Un)Select all adapters') }} ({{ selectedAdapters.length }})</em></q-item-label>
							</q-item-section>
						</q-item>
						<q-item tag="label" v-ripple v-for="adapter in adapterList" :key="adapter.namespace">
							<q-item-section side>
								<q-checkbox v-model="selectedAdapters" :val="adapter.namespace" />
							</q-item-section>
							
							<q-item-section>
								<q-item-label>
									{{ adapter.name }}
									
									<icon name="info" color="primary" class="q-ma-xs" style="font-size: 1.4rem" />
									<q-tooltip content-class="bg-grey-8" :offset="[4, 4]">{{ $t('import#warning').replace('{deviceObjectType}', adapter.deviceObjectType || 'device').replace('{devicePattern}', adapter.devicePattern) }}</q-tooltip>
								</q-item-label>
							</q-item-section>
							
							<q-item-section side>
								<q-item-label>
									<q-chip color="primary" text-color="white" v-if="state === 'loaded' && selectedAdapters.indexOf(adapter.namespace) > -1">
										{{ $t('Devices') }}: {{ adapter.devices }}
									</q-chip>
								</q-item-label>
							</q-item-section>
						</q-item>
					</q-list>
				</q-expansion-item>
				
				<!-- RESULTS -->
				<div v-if="state !== null" style="margin-bottom: 16px">
					<div class="text-h6 text-header primary">{{ $t('Devices') }}</div>
					<q-separator style="margin: 0 -16px 16px -16px" />
					
					<div class="q-pa-sm">
						<q-spinner color="primary" size="md" v-if="state === 'loading'" />
					</div>
					
					<div v-if="state === 'loaded'">
						<q-expansion-item
							v-model="expanded[key]"
							class="paper shadow-1 rounded-borders full-width"
							v-for="(name, key) in categories"
							:key="key"
							:disable="importedDevicesLength[key] === 0"
							style="margin-bottom: 8px"
							>
							
							<template v-slot:header>
								<q-item-section>
									{{ $t(name) }}
								</q-item-section>

								<q-item-section side>
									<q-chip color="secondary" text-color="white">
										{{ importedDevicesLength[key] }}
									</q-chip>
								</q-item-section>
							</template>
							
							<q-list dense v-if="importedDevicesLength[key] > 0">
								<q-item tag="label" v-ripple @click="toggleAllDevicesListItems(key)" v-if="key === 'new' || key === 'existing'">
									<q-item-section side>
										<q-checkbox :value="selectedDevices[key].length !== 0" />
									</q-item-section>

									<q-item-section>
										<q-item-label><em>{{ $t('(Un)Select all devices') }} ({{ selectedDevices[key].length }})</em></q-item-label>
									</q-item-section>
								</q-item>
								<q-expansion-item
									expand-icon-toggle
									expand-separator
									dense
									class="full-width"
									v-for="device in importedDevices[key]" :key="device.id"
									>
									
									<template v-slot:header>
										<q-item tag="label" v-ripple :disable="key !== 'new' && key !== 'existing'" class="full-width">
											<q-item-section side>
												<q-checkbox v-model="selectedDevices[key]" :val="device.id" v-if="key === 'new' || key === 'existing'" />
												<div v-else style="width: 40px"></div>
											</q-item-section>
											
											<q-item-section>
												<q-item-label>
													{{ device.name }}
													
													<q-chip color="accent" text-color="white" v-if="device.attributes && device.attributes.manufacturer">
														{{ device.attributes.manufacturer.name }}
													</q-chip>
												</q-item-label>
											</q-item-section>
											
											<q-item-section side>
												<q-item-label>
													<q-btn flat :label="$t('ignore')" color="negative" @click="ignoreDevice(device)" v-if="key === 'new'" />
													<q-btn flat :label="$t('restore')" color="positive" @click="restoreDevice(device)" v-if="key === 'ignored'" />
												</q-item-label>
											</q-item-section>
										</q-item>
									</template>
									
									<div style="padding-left: 72px">
										{{ $t('Function') }}: {{ $t(device.function) }}
										
										<ul class="stateList">
											<li v-for="(state, stateKey) in device.states" :key="stateKey">
												<strong>{{ stateKey }}</strong>
												<ul class="stateKeyList" v-for="(val, key) in state" :key="key">
													<li>{{ $t(ucFirst(key)) }}: <code>{{ val }}</code></li>
												</ul>
											</li>
										</ul>
									</div>
								</q-expansion-item>
							</q-list>
						</q-expansion-item>
					</div>
				</div>
				
			</q-card-section>
			
			<q-separator />
			<q-card-actions align="right">
				<q-btn flat :label="$t('Cancel')" color="negative" @click="onClose" />
				<q-btn flat :label="$t('Read adapters')" color="secondary" @click="readAdapters" :loading="state === 'loading'" :disable="selectedAdapters.length === 0" />
				<q-btn flat :label="$t('Import selected devices') + ' (' + importDevicesLength + ')'" color="primary" @click="importDevices" v-if="state === 'loaded'" :disable="importDevicesLength === 0"/>
			</q-card-actions>
		</q-card>
	</q-dialog>
	
</template>

<script>
import { defineComponent } from '@vue/composition-api';

import Adapters from 'src/config/adapters';
import ioBroker from 'src/controllers/ioBroker';
import Log from 'src/controllers/Log';

import { ucFirst } from 'src/helpers/utils';

import i18n from './i18n';
export default defineComponent({
	'name': 'ImportDevices',
	'props': [ 'open' ],
	
	data() {
		return {
			'selectedAdapters': [],
			'adapterStructure': 'all',
			'adapterStructureOptions': [
				{
					'label': this.$i18n.t('Search adapter structure for all devices'),
					'value': 'all'
				}, {
					'label': this.$i18n.t('Search only for devices added to enums'),
					'value': 'enums'
				}
			],
			'categories': {
				'new': 'New Devices',
				'existing': 'Devices that have been added already',
				'incorrect': 'Devices with incorrect configuration (states not found)',
				'ignored': 'Devices marked as ignored'
			},
			'expanded': {
				'_adapterList': true,
				'new': true
			},
			'importedDevices': {
				'all': {},
				'new': {},
				'existing': {},
				'incorrect': {},
				'ignored': {},
				'manufacturer': {}
			},
			'selectedDevices': {
				'new': [],
				'existing': [],
				'ignored': []
			},
			'showImportDevices': false,
			'state': null
		}
	},
	
	'methods': {
		importDevices() {
			this.state = null;
			this.onClose();
			const devices = {};
			
			//  devices
			const deviceIds = [ ...this.selectedDevices.new, ...this.selectedDevices.existing ];
			for (const deviceId of deviceIds) {
				devices[deviceId] = this.importedDevices.all[deviceId];
			}
			
			// ignored devices
			const ignoredDevices = Object.keys(this.importedDevices.ignored);
			for (const deviceId of ignoredDevices) {
				devices[deviceId] = this.importedDevices.ignored[deviceId];
				devices[deviceId].attributes = devices[deviceId].attributes || {};
				devices[deviceId].attributes.ignore = true;
			}
			
			this.$store.commit('editor/addDevices', { devices });
		},
		onClose() {
			this.expanded._adapterList = true;
			this.showImportDevices = false;
			this.$emit('onClose', this.showImportDevices);
		},
		readAdapters() {
			this.state = 'loading';
			
			/*
			 * retrieve devices
			 */
			if (this.adapterStructure === 'all') {
				ioBroker.importDevices(this.selectedAdapters)
					.then(this.setImportedDevices)
					.catch(err => {
						Log._('ImportDevicesPage', 'IMPORT_DEVICES: ' + err.message, 'error');
					});
			}
			
			/*
			 * retrieve enums
			 */
			else {
				ioBroker.importEnums('functions', this.selectedAdapters)
					.then(this.setImportedDevices)
					.catch(err => {
						Log._('ImportDevicesPage', 'IMPORT_ENUMS: ' + err.message, 'error');
					});
			}
		},
		setImportedDevices(foundDevices) {
			this.expanded._adapterList = false;
			
			// reset devices
			this.importedDevices = {
				'all': {},
				'new': {},
				'existing': {},
				'incorrect': {},
				'ignored': {},
				'manufacturer': {}
			}
			
			const deviceList = this.$store.state.editor.devices || {};
			foundDevices.forEach((res, index) => {
				const device = res.value || res.reason.device;
				device.attributes = device.attributes || {}
				
				// get category
				let categoryKey = 'new';
				if (res.status === 'fulfilled' && res.value && res.value.states && Object.keys(res.value.states).length > 0) {
					// device added already
					if (deviceList[device.id] !== undefined) {
						categoryKey = 'existing';
						
						// device has been ignored
						if (deviceList[device.id].attributes && deviceList[device.id].attributes.ignore === true) {
							categoryKey = 'ignored';
						}
					}
				}
				else {
					categoryKey = 'incorrect';
					device.states = null;
					device.attributes.note = device.attributes.note || (res.reason && res.reason.error && res.reason.error.message) || this.$i18n.t('No correct defined states found. The device may be unknown. Please help the developer to add support for this device and report to Github.');
				}
				
				// by category
				this.importedDevices[categoryKey][device.id] = device;
				this.importedDevices.all[device.id] = device
				
				// by manufacturer
				if (device.attributes.manufacturer && device.attributes.manufacturer.namespace) {
					this.importedDevices.manufacturer[device.attributes.manufacturer.namespace] = this.importedDevices.manufacturer[device.attributes.manufacturer.namespace] || [];
					this.importedDevices.manufacturer[device.attributes.manufacturer.namespace].push(device.id);
				}
			});
			
			this.selectedDevices.new = Object.keys(this.importedDevices.new);
			this.state = 'loaded';
		},
		toggleAllAdapterListItems() {
			// unselect all
			if (this.selectedAdapters.length > 0) {
				this.selectedAdapters = [];
			}
			
			// select all
			else {
				this.selectedAdapters = Object.keys(Adapters);
			}
		},
		toggleAllDevicesListItems(categoryKey) {
			// unselect all
			if (this.selectedDevices[categoryKey].length > 0) {
				this.selectedDevices[categoryKey] = [];
			}
			
			// select all
			else {
				this.selectedDevices[categoryKey] = Object.keys(this.importedDevices[categoryKey]);
			}
		},
		ignoreDevice(device) {
			this.importedDevices.ignored[device.id] = device;
			this.$delete(this.importedDevices.new, device.id);
			this.$delete(this.selectedDevices.new, this.selectedDevices.new.indexOf(device.id));
		},
		restoreDevice(device) {
			this.importedDevices.new[device.id] = device;
			this.selectedDevices.new.push(device.id);
			this.$delete(this.importedDevices.ignored, device.id);
		},
		"ucFirst": ucFirst
	},
	
	'computed': {
		importDevicesLength() {
			return this.selectedDevices.new.length + this.selectedDevices.existing.length;
		},
		importedDevicesLength() {
			return {
				'all': Object.keys(this.importedDevices.all).length,
				'new': Object.keys(this.importedDevices.new).length,
				'existing': Object.keys(this.importedDevices.existing).length,
				'incorrect': Object.keys(this.importedDevices.incorrect).length,
				'ignored': Object.keys(this.importedDevices.ignored).length
			}
		},
		adapterList() {
			// add found devices
			const adapterList = Object.values(Adapters).map(adapter => {
				adapter.devices = this.importedDevices.manufacturer[adapter.namespace] ? this.importedDevices.manufacturer[adapter.namespace].length : 0;
				adapter.name = adapter.default;
				return adapter;
			});
			
			return adapterList;
		}
	},
	
	'watch': {
		open(val) {
			this.showImportDevices = val;
		}
	},
	
	created() {
		this.toggleAllAdapterListItems();
		i18n.register(this.$i18n, i18n.translations);
	}
});
</script>

<style lang="scss">
.stateList {
	list-style-type: square;
	padding: 0 0 0 16px;
	margin: 8px 0;
}

.stateKeyList {
	list-style-type: none;
	padding: 0 0 0 16px;
	margin: 8px 0;
}
</style>
