<template>
	<div class="q-pa-md">
		<q-table
			:data="devices"
			:columns="columns"
			:pagination="pagination"
			:rows-per-page-label="$t('Devices per page')"
			>
			
			<template v-slot:header="props">
				<q-tr :props="props">
					<q-th auto-width />
					<q-th
						v-for="col in props.cols"
						:key="col.name"
						:props="props"
						>
						
						{{ col.label }}
					</q-th>
					<q-th auto-width />
				</q-tr>
			</template>
			
			<template v-slot:body="props">
				<q-tr :props="props" :class="{ 'rowDeleted': isDeleted(props.row) }">
					<q-td auto-width>
						<div v-if="isDeleted(props.row) !== true">
							<q-btn size="sm" flat round @click="onDeviceEdit(props.row)" icon="mdi-pencil" />
							<q-btn size="sm" flat round @click="onDeviceCopy(props.row)" icon="mdi-content-copy" />
						</div>
					</q-td>
					<q-td
						v-for="col in props.cols"
						:key="col.name"
						:props="props"
						>
						
						<div :class="{ 'itemDeleted': isDeleted(props.row) }">
							{{ col.value }}
						</div>
					</q-td>
					<q-td auto-width>
						<q-btn v-if="isDeleted(props.row) !== true" size="sm" flat round @click="onDeviceRemove(props.row)" icon="mdi-delete" color="negative" />
						<q-btn v-if="isDeleted(props.row)" size="sm" flat round @click="onDeviceRestore(props.row)" icon="mdi-restore" color="positive" />
					</q-td>
				</q-tr>
			</template>
		</q-table>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

export default defineComponent({
	'name': 'DevicesTable',
	'props': [ 'devices' ],
	
	data() {
		return {
			'pagination': {
				'sortBy': 'name',
				'rowsPerPage': 15
			},
			'columns': [{
					'name': 'id',
					'label': this.$i18n.t('ID'),
					'field': 'id',
					'align': 'left',
					'sortable': true,
					'style': {
						'width': '20%'
					}
				}, {
					'name': 'name',
					'label': this.$i18n.t('Name'),
					'field': 'name',
					'align': 'left',
					'sortable': true,
					'style': {
						'width': '25%'
					}
				}, {
					'name': 'manufacturer',
					'label': this.$i18n.t('Manufacturer'),
					'field': 'manufacturer',
					'align': 'left',
					'sortable': true,
					'format': (val, row) => row.attributes && row.attributes.manufacturer ? row.attributes.manufacturer.name : '',
					'style': {
						'width': '15%'
					}
				}, /*{
					'name': 'function',
					'label': this.$i18n.t('Function'),
					'field': 'function',
					'align': 'left',
					'sortable': true
				}, */{
					'name': 'states',
					'label': this.$i18n.t('States'),
					'field': 'states',
					'align': 'left',
					'sortable': true,
					'format': val => {
						val = Object.keys(val || '{}').join(', ');
						return val.length > 90 ? val.substr(0, 88) + ' ...' : val;
					},
					'style': {
						'width': '40%'
					}
				}/*, {
					'name': 'options',
					'label': this.$i18n.t('Options'),
					'field': 'options',
					'align': 'left',
					'sortable': true
				}, {
					'name': 'attributes',
					'label': this.$i18n.t('Attributes'),
					'field': 'attributes',
					'align': 'left',
					'sortable': true
				}*/
			]
		}
	},
	
	'methods': {
		isDeleted(device) {
			return device.attributes && device.attributes.deleted === true;
		},
		onDeviceEdit(device) {
			this.$store.commit('editor/editDevice', { device });
		},
		onDeviceCopy(device) {
			this.$store.commit('editor/copyDevice', { device });
		},
		onDeviceRemove(device) {
			this.$store.commit('editor/removeDevice', { device });
		},
		onDeviceRestore(device) {
			this.$store.commit('editor/restoreDevice', { device });
		}
	}
});
</script>
