<template>
	<div style="padding-bottom: 16px">
		<paper class="StateExplorerBar">
			<inputs @onChange="onFilter" label="Filter" value="" style="width: 250px" />
			<q-space />
			
			<div class="items-center justify-end row">
				<span style="padding-right: 8px">{{ $t('Last Update') }}: {{ lastUpdate }}</span>
				<q-btn color="primary" :label="$t('Refresh')" icon="refresh" @click="updateObjects" :loading="loading" :disable="loading" />
			</div>
		</paper>
		
		<paper class="q-pa-sm">
			<q-tree
				ref="StateExplorer"
				:nodes="instanceList"
				node-key="_id"
				label-key="label"
				@lazy-load="onLazyLoad"
				:filter="filter"
				:filterMethod="filterMethod"
				selected-color="primary"
				:selected.sync="selected"
				@update:selected="onSelect"
				>
				
				<template v-slot:default-header="prop">
					<div class="row full-width" :class="{ item: (prop.node.type === 'state') }">
						<div class="overflow-dots" style="width: 5%"><icon :color="selected && selected.endsWith(prop.node.label) ? 'accent' : 'primary'" :name="prop.node.type === 'state' ? (selected && selected.endsWith(prop.node.label) ? 'mdi-bookmark' : 'mdi-bookmark-outline') : 'folder'" /></div>
						<div class="overflow-dots q-pr-xs" style="width: 25%">{{ prop.node.label }}</div>
						<div class="overflow-dots q-pr-xs" style="width: 45%">
							<q-tooltip v-if="prop.node.common && prop.node.common.name && prop.node.common.name.length > 50">
								{{ prop.node.common.name }}
							</q-tooltip>
							{{ prop.node.common && prop.node.common.name ? (prop.node.common.name.length > 50 ? prop.node.common.name.substr(0,47) + '...' : prop.node.common.name) : '' }}
						</div>
						<div class="overflow-dots" style="width: 10%">{{ prop.node.type }}</div>
						<div class="overflow-dots" style="width: 15%">{{ prop.node.common && prop.node.common.role ? prop.node.common.role : '' }}</div>
					</div>
				</template>
			</q-tree>
		</paper>
	</div>
</template>

<script lang="ts">
import { defineComponent } from '@vue/composition-api';

import Cache from 'src/controllers/Cache';
import ioBroker from 'src/controllers/ioBroker';

import { format } from 'src/helpers/timeConverter';

export default defineComponent({
	'name': 'StateExplorer',
	'props': [
		'id'
	],
	
	created() {
		this.getInstanceList();
	},
	
	data() {
		return {
			'filter': '',
			'lastUpdate': format(Cache.get('objectsUpdated'), 'dd. MMMM yyyy, HH:mm:ss'),
			'loading': false,
			'objects': Cache.get('objects'),
			'objectList': Cache.get('objectList'),
			'objectTree': {},
			'instanceList': [],
			'selected': ''
		}
	},
	
	'methods': {
		onLazyLoad({ key, done }) {
			done((this.objectTree[key] && this.objectTree[key].children) || []);
		},
		onFilter(props) {
			this.filter = props.value;
		},
		onSelect(key) {
			// expand key if it holds children
			if (this.objectTree[key] && this.objectTree[key].children) {
				this.$refs.StateExplorer.setExpanded(key, true);
			}
			
			this.$emit('onChange', { 'id': this.id || 'StateExplorer', 'value': key || '' });
		},
		filterMethod(node, filter) {
			return node._id.indexOf(filter.toLowerCase()) > -1;
		},
		updateObjects() {
			this.loading = true;
			
			ioBroker.getObjects().then(() => {
				this.lastUpdate = format(Cache.get('objectsUpdated'), 'dd. MMMM yyyy, HH:mm:ss');
				this.objects = Cache.get('objects');
				this.objectList = Cache.get('objectList');
				this.loading = false;
			});
		},
		getInstanceList() {
			const objectTree = {}
			const instanceList = [];
			
			this.objectList.forEach(objectId => {
				const object = this.objects[objectId];
				const parentId = object._id.substr(0, object._id.lastIndexOf('.'));
				
				// get root
				const root = object._id.substr(0, object._id.indexOf('.', object._id.indexOf('.') + 1));
				if (root && !instanceList.find(instance => instance._id === root)) {
					instanceList.push({
						'_id': root,
						'label': root,
						'lazy': true
					});
				}
				
				// add to parent
				objectTree[parentId] = objectTree[parentId] || { 'children': [] };
				objectTree[parentId].children.push({
					...object,
					'label': object._id.substr(object._id.lastIndexOf('.') + 1),
					'lazy': object.type !== 'state'
				});
			});
			
			this.objectTree = objectTree;
			this.instanceList = instanceList.sort((a, b) => a.label === b.label ? 0 : (a.label > b.label ? 1 : -1));
		}
	}
});
</script>

<style lang="scss">
.StateExplorerBar {
	margin-bottom: 16px;
    display: flex;
    padding: 0 16px;
}
</style>
