<template>
	<div>
		<state-explorer-popup
			:open="stateExplorer !== null"
			@onClose="onStateExplorerClose"
			@onSave="onStateExplorerSave"
			/>
		
		<paper style="margin: -2px 0 0 0; padding: 0 0 10px 72px" v-show="editStateKey === true">
			<inputs @onChange="onChange" id="stateKeyModified" @click="stopPropagation" label="" :value="stateKey" hide-bottom-space dense />
			
			<div style="margin: auto 86px -1px auto">
				<q-btn size="sm" flat round @click="onStateSave" icon="mdi-check" />
				<q-btn size="sm" flat round @click="onStateEdit" icon="mdi-cancel" />
			</div>
		</paper>
		
		<q-expansion-item
			v-show="editStateKey === false"
			v-model="expanded"
			icon="mdi-drag-horizontal"
			:label="stateKey"
			class="paper full-width"
			:class="{ 'rowDeleted': deleted }"
			:disable="deleted"
			>
			
			<template v-slot:header>
				<q-item-section avatar class="dragndrop">
					<icon name="mdi-drag-horizontal" class="dragndrop" />
				</q-item-section>

				<q-item-section :class="{ 'itemDeleted': deleted }">
					{{ stateKey }}
				</q-item-section>

				<q-item-section side>
					<div class="row items-center">
						<div v-show="deleted !== true">
							<q-btn size="sm" flat round @click="onStateEdit" icon="mdi-pencil" v-show="editStateKey === false" />
							<q-btn size="sm" flat round @click="onStateCopy" icon="mdi-content-copy" />
							<q-btn size="sm" flat round @click="onStateRemove" icon="mdi-delete" color="negative" />
						</div>
						<div v-show="deleted === true">
							<q-btn size="sm" flat round @click="onStateRestore" icon="mdi-restore" color="positive" />
						</div>
					</div>
				</q-item-section>
			</template>
			
			<div class="row" style="padding: 0 8px">
				<!-- STATE -->
				<div class="col">
					<inputs @onChange="onChange" @onSelect="onChange" id="state" label="State" :value="sState" info="mdi-plus" type="select" :options="stateList" options-dense use-input allow-user-input clearable max-dropdown-options="7" hide-dropdown-icon>
						<template v-slot:end>
							<q-btn round dense flat icon="mdi-database-search" color="secondary" @click="onStateExplorerOpen('state', $event)" />
						</template>
					</inputs>
				</div>
				
				<!-- ACTION -->
				<div class="col">
					<inputs @onChange="onChange" @onSelect="onChange" id="action" label="Action" :value="sAction" info="mdi-plus" type="select" :options="stateList" options-dense use-input allow-user-input clearable max-dropdown-options="7" hide-dropdown-icon>
						<template v-slot:end>
							<q-btn round dense flat icon="mdi-database-search" color="secondary" @click="onStateExplorerOpen('action', $event)" />
						</template>
					</inputs>
				</div>
			</div>
			
			<div class="row" style="padding: 0 8px">
				<!-- DISPLAY -->
				<div class="col">
					<inputs @onChange="onChange" id="display" label="Display" :value="sDisplay" info="mdi-plus" json :placeholder="placeholders.display" />
				</div>
			</div>
				
			<div class="row" style="padding: 0 8px">
				<!-- STATE STYLE -->
				<div class="col">
					<inputs @onChange="onChange" id="stateStyle" label="State Style" :value="sStateStyle" info="mdi-plus" json :placeholder="placeholders.stateStyle" />
				</div>
			</div>
				
			<div class="row" style="padding: 0 8px">
				<!-- PROPERTIES -->
				<div class="col">
					<inputs @onChange="onChange" id="properties" label="Properties" :value="sProperties" info="mdi-plus" json :placeholder="placeholders.properties" />
				</div>
			</div>
			
			<div class="row" style="padding: 0 8px">
				<!-- LABEL -->
				<div class="col">
					<inputs @onChange="onChange" id="label" label="Label" :value="sLabel" info="mdi-plus" :placeholder="placeholders.label" />
				</div>
				
				<!-- UNIT -->
				<div class="col">
					<inputs @onChange="onChange" id="unit" label="Unit" :value="sUnit" info="mdi-plus" json="allowTextInput" :placeholder="placeholders.unit" />
				</div>
			</div>
			
			<div class="row" style="padding: 0 8px">
				<!-- ICON -->
				<div class="col">
					<inputs @onChange="onChange" id="icon" label="Icon" :value="sIcon" info="mdi-plus" json="allowTextInput" :placeholder="placeholders.icon" />
				</div>
				
				<!-- ICON STYLE -->
				<div class="col">
					<inputs @onChange="onChange" id="iconStyle" label="Icon style" :value="sIconStyle" info="mdi-plus" json :placeholder="placeholders.iconStyle" />
				</div>
			</div>
			
			<div class="row" style="padding: 0 8px">
				<!-- BODY ELEMENT -->
				<div class="col">
					<inputs
						@onSelect="props => onChange({ ...props, 'value': props.val || null })"
						@onOptions="onChange"
						id="bodyElement"
						label="Body Element"
						:value="sBodyElement || (sBodyElement !== null && placeholders.bodyElement) || null"
						info="mdi-plus"
						type="select.components"
						:componentValues="sBodyElementComponentValues"
						:components="FUNCTION_COMPONENTS['bodyElements'][device.function]"
						/>
					
				</div>
				
				<!-- STATE -->
				<div class="col-2">
					<inputs
						type="Switch"
						@onChange="onChange"
						id="showState"
						label="Show State"
						:value="sShowState"
						/>
					
				</div>
				
				<!-- ACTION ELEMENT -->
				<div class="col">
					<inputs
						@onSelect="props => onChange({ ...props, 'value': props.val || null })"
						@onOptions="onChange"
						id="actionElement"
						label="Action Element"
						:value="sActionElement || (sActionElement !== null && placeholders.actionElement) || null"
						info="mdi-plus"
						type="select.components"
						:componentValues="sActionElementComponentValues"
						:components="FUNCTION_COMPONENTS['actionElements'][device.function]"
						/>
					
				</div>
			</div>
			<q-separator style="margin-top: 16px" />
			
		</q-expansion-item>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

import StateExplorerPopup from 'src/components/state-explorer-popup.vue';

import FUNCTION_CONFIGURATION, { listOfComponents as FUNCTION_COMPONENTS } from 'src/functions/';

export default defineComponent({
	'name': 'DeviceState',
	'props': [ 'device', 'stateKey', 'stateList' ],
	
	'components': {
		StateExplorerPopup
	},
	
	data() {
		this.FUNCTION_COMPONENTS = FUNCTION_COMPONENTS;
		
		return {
			'deleted': false,
			'editStateKey': false,
			'expanded': false,
			'stateExplorer': null,
			'stateKeyModified': ''
		}
	},
	
	'methods': {
		stopPropagation(e) {
			e.stopPropagation();
		},
		onChange(props) {
			// catch error
			if (props._error) {
				//console.warn(props.id, props._error);
				
				this.$emit('onChange', {
					...props,
					'error': props._error,
					'stateKey': this.stateKey,
					'id': 'state.' + props.id
				});
			}
			
			// change state key
			else if (props.id === 'stateKeyModified') {
				this.stateKeyModified = props.value;
			}
			
			// any other
			else {
				let value = props.value;
				try {
					value = value && props.json !== undefined && props.value.indexOf('{') > -1 && props.value.indexOf('}') > -1 ? JSON.parse(props.value) : props.value;
				}
				catch (err) {
					console.warn(props.value, err);
				}
				
				this.$emit('onChange', {
					...props,
					value,
					'stateKey': this.stateKey,
					'id': 'state.' + props.id
				});
			}
		},
		onStateCopy(e) {
			e.stopPropagation();
			
			this.$store.commit('editor/copyState', {
				'stateKey': this.stateKey
			});
		},
		onStateSave(e) {
			e.stopPropagation();
			
			this.$emit('onChange', {
				'stateKey': this.stateKey,
				'id': 'state.newStateKey',
				'value': this.stateKeyModified
			});
			
			this.editStateKey = !this.editStateKey;
		},
		onStateEdit(e) {
			e.stopPropagation();
			
			this.stateKeyModified = this.stateKey;
			this.editStateKey = !this.editStateKey;
		},
		onStateRemove(e) {
			e.stopPropagation();
			
			this.expanded = false;
			this.deleted = true;
			
			this.$store.commit('editor/removeState', {
				'stateKey': this.stateKey
			});
		},
		onStateRestore(e) {
			e.stopPropagation();
			
			this.deleted = false;
			this.$store.commit('editor/restoreState', {
				'stateKey': this.stateKey
			});
		},
		onStateExplorerOpen(field, e) {
			e.stopPropagation();
			this.stateExplorer = field;
		},
		onStateExplorerClose() {
			this.stateExplorer = null;
		},
		onStateExplorerSave({ value }) {
			this.onChange({ 'id': this.stateExplorer, 'stateKey': this.stateKey, 'value': value });
			this.stateExplorer = null;
		}
	},
	
	'computed': {
		placeholders() {
			const placeholders = FUNCTION_CONFIGURATION[this.deviceFunction][this.stateKey.toLowerCase()] || {};
			
			return {
				'display': JSON.stringify(placeholders.display),
				'stateStyle': JSON.stringify(placeholders.stateStyle),
				'properties': JSON.stringify(placeholders.properties),
				'label': placeholders.label || '',
				'unit': JSON.stringify(placeholders.unit),
				'icon': JSON.stringify(placeholders.icon),
				'iconStyle': JSON.stringify(placeholders.iconStyle),
				'bodyElement': placeholders.bodyElement || '',
				'actionElement': placeholders.actionElement || ''
			}
		},
		deviceFunction() {
			return this.device.function === 'other' ? '_defaults' : this.device.function;
		},
		sState() {
			const s = this.$store.state.editor.device.states[this.stateKey];
			return s.state && s.state.node !== undefined ? s.state.node : (s.state || null);
		},
		sAction() {
			const s = this.$store.state.editor.device.states[this.stateKey];
			return s.action && s.action.node !== undefined ? s.action.node : (s.action || null);
		},
		sProperties() {
			const s = this.$store.state.editor.device.states[this.stateKey];
			return s.properties || '';
		},
		sStateStyle() {
			const s = this.$store.state.editor.device.states[this.stateKey];
			return s.stateStyle || '';
		},
		sDisplay() {
			const s = this.$store.state.editor.device.states[this.stateKey];
			return s.display || '';
		},
		sLabel() {
			const s = this.$store.state.editor.device.states[this.stateKey];
			return s.label || '';
		},
		sUnit() {
			const s = this.$store.state.editor.device.states[this.stateKey];
			return s.unit || '';
		},
		sIcon() {
			const s = this.$store.state.editor.device.states[this.stateKey];
			return s.icon || '';
		},
		sIconStyle() {
			const s = this.$store.state.editor.device.states[this.stateKey];
			return s.iconStyle || '';
		},
		sBodyElement() {
			const s = this.$store.state.editor.device.states[this.stateKey];
			return s.bodyElement;
		},
		sBodyElementComponentValues() {
			const s = this.$store.state.editor.device.states[this.stateKey];
			return s[this.sBodyElement + 'Config'] || {};
		},
		sActionElement() {
			const s = this.$store.state.editor.device.states[this.stateKey];
			return s.actionElement;
		},
		sShowState() {
			const s = this.$store.state.editor.device.states[this.stateKey];
			return s.showState !== undefined ? s.showState : true;
		},
		sActionElementComponentValues() {
			const s = this.$store.state.editor.device.states[this.stateKey];
			return s[this.sActionElement + 'Config'] || {};
		}
	}
});
</script>
