<template>
	<q-dialog
		:value="open"
		persistent
		>
		
		<q-card class="column full-height" style="width: 1000px; max-width: 80vw;">
			<q-card-section class="row items-center">
				<div class="text-h6">{{ $t('State Explorer') }}</div>
				
				<q-space />
				<q-btn flat round color="grey" icon="close" @click="onClose" />
			</q-card-section>
			<q-separator />
			
			<q-card-section class="col q-pa-sm scroll body">
				<state-explorer @onChange="onChange" />
			</q-card-section>
			
			<q-separator />
			<q-card-actions align="right">
				<q-btn flat :label="$t('Cancel')" color="negative" @click="onClose" />
				<q-btn flat :label="$t('Apply')" color="primary" @click="onSave" :disable="state === ''" />
			</q-card-actions>
		</q-card>
	</q-dialog>
</template>

<script lang="ts">
import { defineComponent } from '@vue/composition-api';

import StateExplorer from 'src/components/state-explorer';

export default defineComponent({
	'name': 'StateExplorerPopup',
	'props': [
		'open'
	],
	
	'components': {
		StateExplorer
	},
	
	data() {
		return {
			'state': ''
		}
	},
	
	'methods': {
		onClose() {
			this.$emit('onClose', { 'id': this.id || 'StateExplorer', 'value': this.state });
		},
		onSave() {
			this.$emit('onSave', { 'id': this.id || 'StateExplorer', 'value': this.state });
		},
		onChange({ value }) {
			this.state = value;
		}
	}
})
</script>
