<template>
	<q-dialog
		:value="device !== null"
		full-height
		persistent
		>
		
		<q-card class="column full-height" style="width: 1000px; max-width: 80vw;" v-if="device !== null">
			<q-card-section class="row items-center">
				<div class="text-h6">{{ $t('Device') + ' ' + device.name }} ({{ $t('Device ID') + ': ' + device.id }})</div>
				
				<q-space />
				<q-btn flat round color="grey" icon="close" @click="cancel" />
			</q-card-section>
			<q-separator />
			
			<q-card-section class="col defaultPadding-2 q-pt-none scroll body">
				
				<!-- GENERAL -->
				<div class="text-h6 text-header primary">{{ $t('General') }}</div>
				<q-separator style="margin: 0 -16px" />
				
				<div class="row">
					<div class="col-3">
						<inputs @onChange="onChange" id="device.name" :label="$t('Device')" :value="device.name || ''" required autofocus />
					</div>
					<div class="col-3">
						<inputs @onChange="onChange" id="device.label" :label="$t('Label')" :value="device.label || ''" />
					</div>
					<div class="col-3">
						<inputs @onSelect="onChange" id="device.function" :label="$t('Function')" :value="device.function === 'other' ? '_defaults' : device.function" type="select" :options="functions" options-dense use-input />
					</div>
					<div class="col-3">
						<inputs @onChange="onChange" id="device.icon" :label="$t('Icon')" :value="device.icon || ''" :icon="icon(device.icon)" />
					</div>
				</div>
				
				<!-- OPTIONS -->
				<div class="text-h6 text-header primary">{{ $t('Options') }}</div>
				<q-separator style="margin: 0 -16px" />
				<div class="row">
					<div class="col-3">
						<inputs @onChange="onChange" type="switch" id="options.suppressPopup" label="Hide Popup" indeterminate-value="false" :value="device.options.suppressPopup || false" />
					</div>
				</div>
				
				<!-- STATES -->
				<div class="text-h6 text-header primary">{{ $t('States') }}</div>
				<q-separator style="margin: 0 -16px" />
				
				<div class="row" style="margin-bottom: 16px">
					<div class="col">
						<inputs @onChange="onChange" id="newStateKey" :value="newStateKey" label="new state key" icon="mdi-plus" @keydown.enter.native="addStateKey" :error="errorNewStateKey !== null">
							<template v-slot:error>
								{{ errorNewStateKey }}
							</template>
						</inputs>
					</div>
					<div class="col self-center">
						<q-btn color="primary" label="add state" icon="mdi-plus-circle" @click="addStateKey" />
					</div>
					<div class="col self-center" style="text-align: right">
						<q-btn color="secondary" label="add default states" :icon="compareStatesToDefaults ? 'check' : 'mdi-playlist-plus'" @click="addDefaultStates" :disable="compareStatesToDefaults" v-if="this.device.function !== '_defaults'" />
					</div>
				</div>
				
				<draggable
					v-model="deviceStates"
					handle=".dragndrop"
					>
					
					<transition-group type="transition" name="flip-list" tag="div">
						<div v-for="state in deviceStates" :key="state.stateKey" class="list-group-item">
							<device-state
								@onChange="onChange"
								@setComponentProps="component = arguments[0]"
								:device="device"
								:stateKey="state.stateKey"
								:stateList="stateList"
								/>
							
						</div>
					</transition-group>
				</draggable>
			</q-card-section>
			
			<q-separator />
			<q-card-actions align="right">
				<q-btn flat :label="$t('Cancel')" color="negative" @click="cancel" />
				<q-btn flat :label="$t('Apply')" color="primary" @click="apply" :disable="hasErrors" />
			</q-card-actions>
		</q-card>
	</q-dialog>
</template>

<script>
import Vue from 'vue';
import { defineComponent } from '@vue/composition-api';

import Cache from 'src/controllers/Cache'

import FUNCTION_CONFIGURATION, { icons as FUNCTION_ICONS } from 'src/functions';

import DeviceState from './DeviceState.vue';
import Draggable from 'vuedraggable';

import { icon } from 'src/helpers/utils';

export default defineComponent({
	'name': 'DevicePage',
	
	'components': {
		DeviceState,
		Draggable
	},
	
	data() {
		this.icon = icon;
		
		return {
			'component': null,
			'errors': {},
			'errorNewStateKey': null,
			'newStateKey': ''
		}
	},
	
	'methods': {
		apply() {
			this.$store.commit('editor/saveDevice');
		},
		cancel() {
			this.$store.commit('editor/discardDevice', { 'device': null });
		},
		onChange(props) {
			// error
			if (props.error) {
				this.errors = {
					...this.errors,
					[props.id]: this.$i18n.t(props.error)
				}
			}
			
			// device
			else if (props.id.indexOf('device.') > -1) {
				Vue.delete(this.errors, props.id);
				this.$store.commit('editor/set', { [props.id]: props.value });
			}
			
			// device options
			else if (props.id.indexOf('options.') > -1) {
				Vue.delete(this.errors, props.id);
				
				this.$store.commit('editor/set', {
					'device.options': {
						...this.device.options || {},
						[props.id.replace('options.', '')]: props.value
					}
				});
			}
			
			// device state
			else if (props.id.indexOf('state.') > -1) {
				Vue.delete(this.errors, props.id);
				
				// change state key
				if (props.id === 'state.newStateKey') {
					this.$store.commit('editor/changeStateKey', {
						'stateKey': props.stateKey,
						'stateKeyModified': props.value
					});
				}
				
				// device state options
				else if (props.id === 'state.componentOptions') {
					Vue.delete(this.errors, props.id);
					
					const componentOptions = this.$store.state.editor.device.states[props.stateKey][props.component + 'Config'] || {};
					this.$store.commit('editor/editState', {
						'deviceId': this.device.id,
						'stateKey': props.stateKey,
						'key': props.component + 'Config',
						'val': {
							...componentOptions,
							...props.value
						}
					});
				}
				
				// change any other value
				else {
					this.$store.commit('editor/editState', {
						'deviceId': this.device.id,
						'stateKey': props.stateKey,
						'key': props.id.replace('state.', ''),
						'val': props.value
					});
				}
			}
			
			// new state key
			else if (props.id === 'newStateKey') {
				this.newStateKey = props.value;
			}
		},
		addDefaultStates() {
			this.getDefaultStates.forEach(defaultState => defaultState !== '_any' && this.addStateKey(defaultState));
		},
		addStateKey(key) {
			key = typeof key === 'string' ? key : this.newStateKey;
			
			// no input given
			if (!key) {
				this.errorNewStateKey = this.$i18n.t('Type in a state key / identifier');
			}
			
			// stateKey is duplicated
			else if (this.deviceStateKeys.indexOf(key) > -1) {
				this.errorNewStateKey = this.$i18n.t('State key already used');
			}
			
			// add state key
			else {
				this.$store.commit('editor/newState', {
					'stateKey': key
				});
				
				this.newStateKey = '';
				this.errorNewStateKey = null;
			}
		}
	},
	
	'computed': {
		getDefaultStates() {
			return this.device.function === '_defaults' ? [] : Object.keys(FUNCTION_CONFIGURATION[this.device.function] || {});
		},
		compareStatesToDefaults() {
			const states = Object.keys(this.device.states || {});
			return this.getDefaultStates.every(item => item === '_any' || states.indexOf(item) > -1);
		},
		stateList() {
			const stateList = Cache.get('stateList') || [];
			return stateList.map(state => ({ 'label': state, 'value': state }));
		},
		functions() {
			return Object.keys(FUNCTION_ICONS).map(funcKey => ({
				'label': this.$i18n.t(funcKey),
				'icon': FUNCTION_ICONS[funcKey] ? 'mdi-' + FUNCTION_ICONS[funcKey].replace('mdi-', '') : '',
				'value': funcKey
			})).sort((a, b) => a.label.toLowerCase() === b.label.toLowerCase() ? 0 : (a.label.toLowerCase() > b.label.toLowerCase() ? 1 : -1));
		},
		device() {
			return this.$store.state.editor.device || null;
		},
		deviceStateKeys() {
			return Object.keys(this.device.states || {});
		},
		'deviceStates': {
			get() {
				const deviceStates = Object.values(this.device.states || {});
				return deviceStates;
			},
			set(states) {
				const deviceStates = {}
				states.forEach(state => {
					deviceStates[state.stateKey] = state;
				});
				
				this.$store.commit('editor/set', { 'device.states': deviceStates });
			}
		},
		hasErrors() {
			return Object.keys(this.errors).length > 0;
		}
	}
});
</script>
