<template>
	<div class="full-height DevicesPage">
		<div class="row no-wrap">
			<q-toolbar class="paper" style="min-height: 56px">
				<q-toolbar-title shrink>{{ $t('Device List') }} ({{ devicesCount }})</q-toolbar-title>
				
				<q-select dense :label="$t('Group by')" stack-label style="width: 150px; margin-right: 8px" v-model="devicesGroupBy" :options="groupByOptions" />
				<q-input dense :label="$t('Filter')" stack-label clearable v-model="devicesFilter" style="width: 250px" />
				
				<q-space />
				<q-btn color="primary" icon="mdi-plus-circle" :label="$t('add device')" style="margin-right: 8px" @click="addDevice" />
				<q-btn color="primary" icon="mdi-cloud-download" :label="$t('import device')" @click="showImportDevices = true" />
			</q-toolbar>
		</div>
		
		<device-page />
		<import-devices
			@onClose="showImportDevices = false"
			:open="showImportDevices"
			/>
		
		<!-- DEVICE LIST -->
		<div v-show="!expertMode && group.devices.length > 0" class="row no-wrap paper" v-for="group in groups" :key="group.id">
			<q-expansion-item
				v-model="expanded[group.id]"
				:icon="group.icon"
				:label="group.label + ' (' + group.devices.length + ')'"
				class="full-width"
				>
				
				<devices-table :devices="group.devices" />
			</q-expansion-item>
		</div>
		
		<!-- EXPERT MODE -->
		<paper v-show="expertMode" :style="{ 'height': (pageHeight-66-16) + 'px' }">
			<json-editor id="devices" :json="json" @onChange="onJsonEditor" />
		</paper>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

import DevicePage from '../DevicePage/DevicePage.vue';
import ImportDevices from '../ImportDevices/ImportDevices.vue';
import DevicesTable from './DevicesTable.vue';

import JsonEditor from 'src/components/json-editor.vue';

import i18n from './i18n';

export default defineComponent({
	'name': 'DevicesPage',
	
	'components': {
		DevicePage,
		DevicesTable,
		ImportDevices,
		JsonEditor
	},
	
	data() {
		const groupByOptions = [
			{
				'label': this.$i18n.t('Function'),
				'value': 'function'
			},
			{
				'label': this.$i18n.t('Manufacturer'),
				'value': 'manufacturer'
			}
		];
		
		return {
			'expanded': {},
			'groupByOptions': groupByOptions,
			'showImportDevices': false
		}
	},
	
	'methods': {
		addDevice() {
			this.$store.commit('editor/addDevice');
		},
		onJsonEditor(props) {
			if (!props._error) {
				this.$store.commit('editor/set', { [props.id]: JSON.parse(props.value) });
			}
		},
		onChange({ id, error, value }) {
			//
			if (error) {
				this.$store.commit('editor/addError', { 'key': 'settings-' + id, 'val': error.message || error });
			}
			
			//
			else if (value) {
				this.$store.commit('editor/removeError', { 'key': 'settings-' + id });
				
				for (const key in value) {
					this.$store.commit('editor/set', { ['settings.' + key]: value[key] });
				}
			}
		}
	},
	
	'computed': {
		pageHeight() {
			return this.$store.state.jarvis.pageHeight;
		},
		'devicesGroupBy': {
			get() {
				return (this.$store.state.editor.settings?.configDevicesGroupBy && this.groupByOptions.find(groupBy => groupBy.value === this.$store.state.editor.settings.configDevicesGroupBy)) || this.groupByOptions[0];
			},
			set({ value }) {
				this.$store.commit('editor/set', { 'settings.configDevicesGroupBy': value });
			}
		},
		'devicesFilter': {
			get() {
				return this.$store.state.editor.settings?.configDevicesFilter || '';
			},
			set(val) {
				this.$store.commit('editor/set', { 'settings.configDevicesFilter': val });
			}
		},
		groups() {
			return this.$store.getters['editor/getGroups'](this.devicesGroupBy.value, this.devicesFilter);
		},
		devicesCount() {
			return this.$store.getters['editor/getDevicesWthoutDeletionsCount'];
		},
		json() {
			return this.$store.state.editor.devices;
		},
		expertMode() {
			return this.$store.state.editor.settings?.configExpertMode;
		}
	},
	
	created() {
		i18n.register(this.$i18n, i18n.translations);
	}
});
</script>
